function SingleTweetInfo({ replyCount, likeCount, className }) {
  return (
    <div className={className}>
      <span>
        <strong>{`${replyCount} `}</strong>
        
      </span>
      <span>
        <strong>{`${likeCount} `} </strong>
        
      </span>
    </div>
  )
}

export default SingleTweetInfo
